@import 'variables';

.page {
  color: #c2c2c2;
  scroll-behavior: smooth;


  &__body {
    margin: 0;
    min-width: 320px;
    overflow-x: hidden;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    opacity: 0;
    transition: all 0.3s;
    transform: translateX(-100%);
    pointer-events: none;


    &:target {
      opacity: 1;
      pointer-events: all;
      transform: translateX(0);
    }
  }

  &:has(.page__menu:target) {
    overflow: hidden; /* not to scroll the page */
  }
}
